.contact{padding:30px 16%;display:flex;flex-direction:column}@media(min-width: 768px){.contact{padding:60px 12%;display:grid;grid-template-columns:repeat(2, 1fr);gap:48px}}.contact__title{font-family:"Geometria Medium";font-size:28px;line-height:1.2em;color:#4ca89d;margin:30px 0}@media(min-width: 768px){.contact__title{margin-top:0}}@media(min-width: 1064px){.contact__title{font-family:"Geometria Medium";font-size:52px;line-height:1em}}.contact__text{font-family:"Geometria Light";font-size:18px;font-weight:400;line-height:1.4em;margin-top:0;margin-bottom:30px;color:#000}@media(min-width: 768px){.contact__text{font-family:"Geometria Light";font-size:18px;font-weight:400;line-height:1.4em;margin-top:18px;margin-bottom:50px}}.contact__form{display:flex;flex-direction:column;gap:12px}@media(min-width: 768px){.contact__form{gap:24px}}.contact label{display:flex;flex-direction:column}.contact label span{font-family:"Geometria Light";font-size:14px;font-weight:400;line-height:1.4em;color:#000}.contact input,.contact textarea{width:100%;padding:8px 12px;margin-top:6px;box-sizing:border-box;color:#000;background-color:#fff;border-radius:0;border:1px solid #646464;outline:none;resize:none;font-family:"Geometria Light";font-size:16px;font-weight:400;line-height:1.6em}.contact input:active,.contact input:focus-visible,.contact textarea:active,.contact textarea:focus-visible{color:#646464;border-radius:0;outline:none}.contact .error{color:red;font-size:.85em;margin-top:8px}.contact .successMessage{color:red}.contact__button{display:inline-block;padding:11px 15px;margin-top:24px;width:100%;text-align:center;background-color:#20a69b;color:#fff;font-family:"Geometria Light";font-size:16px;font-weight:400;line-height:1.6em;border-radius:none;text-decoration:none;letter-spacing:0;border:none;cursor:pointer}.contact__button:hover{background-color:#4ca89d;color:#fff}#phone{display:block;width:100%}